	import doRequest from '@/common/request.js'
	 export default {
	        data() {
	            return {
	                avatarUrl: null,
	                hasLogin: uni.getStorageSync('hasLogin'),
					parentUUID: ''
	            };
	        },
	        methods: {
	            //第一授权获取用户信息===》按钮触发
	            wxGetUserInfo() {
					uni.showLoading({
						title: '登录中...'
					});
	                uni.getUserInfo({
	                    provider: 'qq',
	                    success: (infoRes) => {
							this.avatarUrl = infoRes.userInfo.avatarUrl
	                      const data = {
　　　　　　　　　　　　　　　		nickName: infoRes.userInfo.nickName,
								avatarUrl: infoRes.userInfo.avatarUrl,
								city: infoRes.userInfo.city,
								country: infoRes.userInfo.country,
								province: infoRes.userInfo.province,
								parentUUID: this.parentUUID
							}
	                        try {
	                            doRequest('/user/register',data).then(res=>{
									console.log(res);
									uni.setStorageSync('uuid', res.data.uuid)
									uni.setStorageSync('uid', res.data.uid)
									uni.setStorageSync('hasLogin', true);
									this.$store.commit('login', {
										uuid: res.data.uuid,
										uid: res.data.uid,
										userInfo: data,
										task: res.data.task
									})
	                            	uni.reLaunch({
	                            		url: '../tabbar-1/tabbar-1'
	                            	})
	                            })
	                        } catch (e) {}
	                    }
	                });
					uni.hideLoading();
	            },
	            login() {
					if (this.hasLogin) {
						uni.showLoading({
							title: '登录中...'
						});
						uni.getUserInfo({
							provider: 'qq',	
							success: (infoRes)=>{
								this.avatarUrl = infoRes.userInfo.avatarUrl
								let uuid = ''
								try{
									uuid = uni.getStorageSync('uuid')
								}catch(e){
									//TODO handle the exception
								}
								const data = {
									uuid,
									data: {
	 　　　　　　　　　　　　　　　　		nickName: infoRes.userInfo.nickName,
										avatarUrl: infoRes.userInfo.avatarUrl,
										city: infoRes.userInfo.city,
										country: infoRes.userInfo.country,
										province: infoRes.userInfo.province
									}
								}
								doRequest('/user/update', data).then(res=>{
									console.log(res);
									// console.log(JSON.stringify(res.mask));
									this.$store.commit('login', {
										uuid: data.uuid,
										userInfo: data.data,
										task: res.task
									})
									uni.reLaunch({
										url: '../tabbar-1/tabbar-1'
									})
								}).catch(_=>{
									this.$store.commit('login', {
										uuid: data.uuid,
										userInfo: data.data
									})
									uni.reLaunch({
										url: '../tabbar-3/tabbar-3'
									})
								})
							}
						})
						uni.hideLoading();
					}
	            }
	        },
	        onLoad(options) {//默认加载
			console.log('login');
				this.avatarUrl = '../../../static/qq.png'
	            this.login();
				if (options.parentID) {
					this.parentUUID = options.parentID
				}
	        }
	    }
